import React from "react";
import { Link, useTranslation } from "gatsby-plugin-react-i18next";

import { getDate, getSortedEvents } from "../../../hooks/useEditionData";
import { editionData, Edition } from "../../../data";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";

import "react-vertical-timeline-component/style.min.css";
import * as styles from "./schedule.module.css";

type ScheduleProps = {
  edition: Edition;
};

const Schedule: React.FC<ScheduleProps> = ({ edition }) => {
  const { t } = useTranslation();

  const contentStyle = {
    background: "var(--card-header)",
    color: "var(--text-light)",
    boxShadow: "0 3px 0 var(--accent-color)",
  };
  const iconStyle = { borderRight: "7px solid var(--card-header)" };

  return (
    <div className={`page ${styles.schedule}`} id={"schedule"}>
      <h1 className={`title ${styles.scheduleTitle}`}>{t("title")}</h1>
      <h2 className={`date ${styles.scheduleSubtitle}`}>{edition}</h2>

      <VerticalTimeline className={styles.verticalTimeline}>
        {getSortedEvents(edition).map((event, index) => {
          const eventData = editionData[event.title];
          return (
            <VerticalTimelineElement
              key={index}
              contentStyle={contentStyle}
              contentArrowStyle={iconStyle}
              dateClassName={styles.verticalTimelineElementDate}
              iconClassName={styles.scheduleIcon}
              date={getDate(edition, event.title)}
              icon={eventData.eventIcon}
            >
              <Link to={eventData.eventLink} className={styles.link}>
                <h3 className={styles.verticalTimelineElementTitle}>
                  {t(`events.${event.title}`)}
                </h3>
              </Link>
            </VerticalTimelineElement>
          );
        })}
      </VerticalTimeline>
    </div>
  );
};

export default Schedule;
